* ::after, ::before {
    margin: 0;
    padding: 0;
    box-sizing: border-box; }

html {
    font-size: 62.5%; }

body {
    font-family: "Roboto", sans-serif;
    font-weight: 400;
    color: #777777;
    font-size: 1.6rem;
    letter-spacing: .2rem;
    padding: 5rem;
    background-color: white; }

.container {
    background-color: #F7F7F7; }


.header {
    position: relative;
    height: 95vh;
    background: linear-gradient(90deg, rgba(186, 133, 84, 0.7) 0.03%, rgba(1, 1, 1, 0.7) 99.94%), url(../img/header.jpg) center/cover no-repeat;
    clip-path: polygon(0 0%, 100% 0%, 100% 100%, 0% 80%);
    &__logo {
        position: absolute;
        height: 7rem;
        width: 5.6rem;
        left: 5rem;
        top: 5rem; }
    &__box {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%,-50%);
        width: 100%;
        text-align: center; } }
.title-first {
    color: #FFFFFF;
    font-family: 'Roboto',sans-serif;
    &__main {
        display: block;
        font-weight: 800;
        font-size: 5rem;
        line-height: 6rem;
        letter-spacing: 1rem;
        text-transform: uppercase;
        animation-name: lefttoright;
        animation-duration: 4s;
        animation-delay: 2s;
        backface-visibility: hidden; }
    &__secondary {
        display: block;
        font-weight: 100px;
        font-style: normal;
        font-size: 4rem;
        line-height: 4.8rem;
        letter-spacing: .5rem;
        font-family: Roboto Thin,sans-serif;
        src: local("fonts/Roboto Thin.ttf");
        padding: 1.5rem;
        animation-name: righttoleft;
        animation-duration: 6s;
        backface-visibility: hidden;
        margin-bottom: 6.5rem; } }


.btn {
    &:link, &:visited {
        text-transform: uppercase;
        text-decoration: none;
        font-size: 2rem;
        line-height: 2.4rem;
        letter-spacing: .5rem;
        color: #777777;
        padding: 1.5rem;
        background: white;
        border-radius: 5rem;
        display: inline-block;
        transition: all .5s;
        position: relative; }
    &:hover {
        transform: translateY(-3px);
        box-shadow: (0px 4px 4px rgba(0, 0, 0, 0.5));
        &::after {
            opacity: 0;
            transform: scale(1.6); } }
    &:active {
         transform: translateY(-2px); }
    &::after {
        content: "";
        background: white;
        width: 100%;
        height: 100%;
        border-radius: 5rem;
        display: inline-block;
        position: absolute;
        top: 0;
        left: 0;
        z-index: -1;
        transition: all .4s; }
    &_animated {
        animation: BtnToUp .5s ease-out .7s;
        animation-fill-mode: backwards; } }


@keyframes BtnToUp {
    0% {
      opacity: 0;
      transform: translateY(70px); }


    100% {
        opacity: 1;
        transform: translateY(0px); } }



@keyframes lefttoright {
    0% {
      opacity: 0;
      transform: translateX(-100px); }
    80% {
       transform: translateX(20px); }

    100% {
        opacity: 1;
        transform: translateX(0px); } }


@keyframes righttoleft {
    0% {
      opacity: 0;
      transform: translateX(0px); }
    80% {
       transform: translateX(-20px); }

    100% {
        opacity: 1;
        transform: translateX(0px); } }
